	-o-transition: all .3s ease .30s;
	-ms-transition: all .3s ease .30s;
	transition: all .3s ease .30s;
	position: absolute;
}
.cart-header:after {
	border-bottom: 9px solid $white;
	content: "";
	border-left: 8px solid transparent;
	opacity: 0;
	width: 0;
	height: 0;
	border-right: 8px solid transparent;
	position: absolute;
	left: 59%;
	margin-left: -23px;
	bottom: 0px;
	z-index: 99;
	top: 55px;
	visibility: hidden;
	overflow: hidden;
	-webkit-transition: all .3s ease .30s;
	-moz-transition: all .3s ease .30s;
	-o-transition: all .3s ease .30s;
	-ms-transition: all .3s ease .30s;
	transition: all .3s ease .30s;
}
.cart-header:hover:after {
	border-bottom: 9px solid $white;
	content: "";
	border-left: 8px solid transparent;
	opacity: 1;
	width: 0;
	height: 0;
	border-right: 8px solid transparent;
	position: absolute;
	left: 59%;
	margin-left: -23px;
	bottom: 0px;
	z-index: 99;
	top: 60px;
	visibility: visible;
	overflow: visible;
	-webkit-transition: all .3s ease .30s;
	-moz-transition: all .3s ease .30s;
	-o-transition: all .3s ease .30s;
	-ms-transition: all .3s ease .30s;
	transition: all .3s ease .30s;
	@media #{$screen767}{
		visibility: hidden;
		overflow: hidden;
	}
}

