		.control-nav {
			display:none;
			
			@media #{$screen970}{
				position: absolute;
				left: 0px;
				top: 2px;
				display: block;
				width: 25px;
				padding: 5px 0;
				border: solid #fff;
				border-width: 4px 0;
				z-index: 2;
				cursor: pointer;
			}

			@media #{$screen767}{
				left: 15px;
			}
		}

		.control-nav:before {
			content: "";
			display: block;
			height: 4px;
			background: #fff;
		}

		.control-nav-close {
			position: fixed;
			right: 0;
			top: 0;
			bottom: 0;
			left: 0;
			display: none;
			z-index: 1;
			background: rgba(0,0,0,0.4);
			-webkit-transition: all 500ms ease;
			transition: all 500ms ease;
			-webkit-transform: translate(100%, 0);
			-ms-transform: translate(100%, 0);
			transform: translate(100%, 0);

			@media #{$screen970}{
				display:block;
			 }
		}


		#control-nav:checked ~ .control-nav-close {
			-webkit-transform: translate(0, 0);
			-ms-transform: translate(0, 0);
			transform: translate(0, 0);
		}

		#control-nav:checked ~ nav {
			-webkit-transform: translate(0, 0);
			-ms-transform: translate(0, 0);
			transform: translate(0, 0);
		}

		input#control-nav {
			visibility: hidden;
			position: absolute;
			left: -9999px;
			opacity: 0;
		}

